* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

li {
  list-style: none;
  font-weight: 600;
  display: flex;
  align-items: center;
}
span {
  margin-left: 8px;
}

img {
  margin-right: 10px;
}

p {
  margin: 10px 0px;
}

.country-title {
  display: flex;
  align-items: center;
}
